@import "../../libs/include-media/dist/include-media";
@import "../utils/special-version-variables";
@import "../utils/mixins";

.#{$spec-mod} {
	// vertical space - section
	// Самый большой отступ. Между логическими эелементами. Секциями
	// ---------------------------------------------------------------------------
	#header,
	#content,
	.spec-bottom-space-section {
		@include spec-bottom-space-section(); }



	// vertical space - item
	// Отступы между крупными элементами внутри секции
	// ---------------------------------------------------------------------------
	.spec-bottom-space-item {
		@include spec-bottom-space-item(); }



	// vertical space - default
	// Стандартный отступ между элементами
	// ---------------------------------------------------------------------------

	p, blockquote,
	blockquote q,
	blockquote > p,
	input[type="text"],
	input[type="password"],
	input[type="email"],
	input[type="search"],
	textarea,
	.select,
	select,
	.spec-bottom-space {
		@include spec-bottom-space(); }



	// vertical space - element
	// Небольшой отступ
	// ---------------------------------------------------------------------------
	.spec-bottom-space-element {
		@include spec-bottom-space-element(); } }
